html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  flex-direction: column;
  position: relative;
  margin: 0 auto;
  max-width: 1440px;
  min-height: 100vh;
  padding: 0;
  line-height: 1.3;
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
  color: #a0a1ac;
}

.wrapper {
  position: relative;
}

h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

@media (min-width: 1025px) {
  .hide-for-desktop {
    display: none;
  }
}
@media (min-width: 878px) and (max-width: 1024px) {
  .hide-for-desktop {
    display: none;
  }
}

@media (min-width: 280px) and (max-width: 877px) {
  .hide-for-mobile {
    display: none;
  }
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  max-width: 100%;
  background-color: #eef3f6;
  top: -77px;
  right: 0;
  left: 202px;
  height: 50%;
  border-bottom-left-radius: 100px;
  z-index: -1;
}
@media (min-width: 878px) and (max-width: 1024px) {
  .overlay {
    left: 24.75rem;
  }
}
@media (min-width: 1025px) {
  .overlay {
    top: -3.5rem;
    left: 45rem;
  }
}

footer {
  font-weight: bold;
  margin: 1rem 0;
  padding: 1rem;
}

.attribution {
  font-size: 1rem;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

.header {
  position: relative;
  font-family: "Barlow Condensed", sans-serif;
}
.header__logo {
  padding-top: 1.6rem;
}

.menuMobile_btn {
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.mobile-links {
  position: absolute;
  top: -9px;
  left: 21px;
  right: 30px;
  margin-top: 100px;
  background-color: white;
  box-shadow: 7px 6px 15px 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.mobile-links__menu {
  text-align: center;
}
.mobile-links__menu hr {
  height: 3px;
  width: 85%;
  background-color: #eef3f6;
  border: none;
}
.mobile-links a {
  display: block;
  margin-bottom: 1.5rem;
  color: #242942;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
.mobile-links a:first-child {
  margin-top: 1rem;
}
.mobile-links a:last-child {
  padding-top: 0.5rem;
  color: #a0a1ac;
}

.nav-links {
  position: relative;
}
.nav-links a {
  color: #242942;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  text-transform: uppercase;
  text-decoration: none;
  margin-right: 2.8125rem;
  transition: all 300ms ease-in-out;
  cursor: pointer;
}
.nav-links a:hover, .nav-links a:active {
  text-decoration: underline;
}
.nav-links a:last-child {
  color: #a0a1ac;
  margin-left: 1.4rem;
}
.nav-links > span {
  position: absolute;
  top: -142%;
  left: 71%;
  font-size: 3rem;
}

.main {
  padding: 8rem 0;
  display: flex;
  justify-content: space-between;
  padding-left: 9rem;
  overflow: hidden;
}
@media (min-width: 878px) and (max-width: 1024px) {
  .main {
    padding: 5rem 0 5rem 3rem;
  }
}
@media (min-width: 280px) and (max-width: 877px) {
  .main {
    flex-direction: column-reverse;
    padding: 3rem 0;
  }
}

.text {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 5rem 2rem 0;
  font-family: "Barlow Condensed", sans-serif;
  text-transform: uppercase;
}
@media (min-width: 878px) and (max-width: 1024px) {
  .text {
    text-align: left;
  }
}
@media (min-width: 1025px) {
  .text {
    padding-left: 4rem;
  }
}
.text__top > span {
  background-color: #242942;
  color: white;
  border-radius: 50px;
  padding: 5px 12px;
  font-size: 0.875rem;
  font-weight: 700;
}
.text__topTitle {
  padding-left: 0.625rem;
  display: inline-flex;
  letter-spacing: 3px;
  font-size: 1rem;
}
.text__bigTitle h1 {
  color: #242942;
  font-size: 2.2rem;
  font-weight: 700;
}
@media (min-width: 878px) and (max-width: 1024px) {
  .text__bigTitle h1 {
    font-size: 3rem;
    text-align: left;
  }
}
@media (min-width: 1025px) {
  .text__bigTitle h1 {
    width: 25rem;
    font-size: 3rem;
  }
}
.text__para {
  margin-top: 1rem;
  font-size: 1.2rem;
  font-family: "Barlow", sans-serif;
  text-transform: initial;
}
@media (min-width: 1025px) {
  .text__para {
    margin-bottom: 2rem;
    width: 18.75rem;
  }
}
.text__button {
  margin-top: 2rem;
}
.text__button a {
  display: inline-block;
  line-height: 35px;
  text-align: center;
  text-decoration: none;
  background-color: #ff5c5c;
  width: 120px;
  height: 37px;
  border-radius: 3px;
  color: #eef3f6;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.8rem;
}
.text__preview {
  padding-left: 0.625rem;
  text-transform: uppercase;
  font-size: 0.875rem;
  font-weight: 400;
  letter-spacing: 2px;
}
@media (min-width: 1025px) {
  .text__preview {
    padding-left: 1.25rem;
  }
}

@media (min-width: 878px) and (max-width: 1024px) {
  .image {
    margin-right: -11rem;
  }
}
.image img {
  width: 120%;
  margin-left: 3.125rem;
}
@media (min-width: 1025px) {
  .image img {
    width: 129%;
    margin-right: -17.5rem;
  }
}
@media (min-width: 878px) and (max-width: 1024px) {
  .image img {
    width: 40rem;
  }
}